.header {
  margin: auto;
  position: relative;
  background-color: $darkBlue;
  height: 2.5rem;
  width: 100%;
  box-shadow: 0 0.3rem 0.5rem rgba(0, 0, 0, 0.3);
  z-index: 100000;

  &__wrapper {
    position: relative;
    margin: auto;
    height: 100%;
    max-width: 1440px;
    padding-left: $mediumPadding;
  }

  &__logo {
    height: 160%;
    position: absolute;
    z-index: 10000;
    width: 3.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem;
    border-bottom-left-radius: 0.5rem;
    border-bottom-right-radius: 0.5rem;
    background-color: $primary;
    box-shadow: $defaultBoxShadow;
    cursor: pointer;

    img {
      width: 2rem;
    }
  }

  & span {
    display: inline-block;
    position: relative;
    font-weight: bold;
    top: 1rem;
    left: 3.75rem;
    padding-left: 0.5rem;
    color: white;
    transform: scaleX(0);
    transform-origin: left;
  }

}

.header__logo:hover img {
  animation: logoShaking 0.4s infinite;
}

.header__logo:hover+span {
  transform: scaleX(1);
  transition: transform 0.2s
}

@media all and (max-width: 768px) {
  .header__logo {
    width: 3rem;
    height: 130%;

    & img {
      width: 1.5rem;
    }
  }

  .header span {
    left: 3.125rem;
  }
}